const yo = require('yo-yo');
import Soshi from '../core';
class Image extends Soshi {
  el: HTMLElement
  props:{
    src: String,
    alt: String,
    height: Number,
    width: Number,
  }
  template() {
    return yo`<img 
        src="${this.props.src}"
        alt="${this.props.alt}"
        height="${this.props.height}"
        width="${this.props.width}"
    />`;
  }
}
export default Image;



